<template>
  <div class="centerbox dlcx">
    <div class="lan08 f_l">
      <!---->
      <div class="modularbox m_t" style="padding:0 0 20px;">
        <div class="list-screen">
          <div style="padding:30px 10px 10px 30px; position:relative;">
            <h3 class="ifsearch"></h3>
            <div class="screen-term">
              <div class="selectNumberScreen">
                <div id="selectList" class="screenBox screenBackground">
                  <check-box
                    ref="check1"
                    num="1"
                    name=""
                    :hasDuos="false"
                    :data="['','','']"
                    @changeCheck="changeCheck"
                  ></check-box>
                  <!-- <check-box
                    ref="check2"
                    num="2"
                    name=""
                    :data="['','','',
                    '1','1','1',
                    '2','111111','2',]"
                    @changeCheck="changeCheck"
                  ></check-box> -->
                  <check-box
                    ref="check3"
                    num="3"
                    v-show="showMore"
                    name=""
                    :hasDuos="true"
                    :data="['123asd','@2341','','','','',
                      '1','1','1','','']"
                    @changeCheck="changeCheck"
                  ></check-box>
                  <check-box
                    ref="check4"
                    num="4"
                    v-show="showMore"
                    name=""
                    :data="['1','2','3']"
                    @changeCheck="changeCheck"
                  ></check-box>
                  <span @click="showMore=!showMore" :class="['ifmore',{more_bg:showMore}]">
                    <em class="open"></em>
                    {{showMore?'':''}}
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div style="padding:30px 10px 10px 30px; position:relative;">
            <h3 class="ifsearch"></h3>
            <div class="screen-term">
              <div class="dlcs_search">
                <input
                  type="text"
                  name="title"
                  lay-verify="title"
                  autocomplete="off"
                  placeholder=""
                  class="layui-input"
                >
                <span class="layui-btn ssbutt yun_icon_search" title=""></span>
                <span class="layui-btn ssbutt"></span>
              </div>
            </div>
          </div>
          <div class="hasBeenSelected clearfix">
            <div style="float:right;" @click="delCheckAll" class="eliminateCriteria"></div>
            <dl style="overflow-y:auto;max-height:81px;">
                <dt></dt>
                <dd class="clearDd" style="width:1026px;">
                  <div class="clearList">
                    <template v-for="(item,index) in [filter1,filter2,filter3,filter4]">
                      <div
                        class="selectedInfor selectedShow"
                        :key="index+indexd"
                        v-for="(itemd,indexd) in item"
                      >
                        <span>{{filterNames[index]}}</span>
                        <label>{{itemd}}</label>
                        <em p="2" @click="delCheck(index,indexd)"></em>
                      </div>
                    </template>
                  </div>
                </dd>
              
            </dl>
          </div>
        </div>
        <!--END-->
        <div class="sctjdatabox layui-form">
          :
          <input type="radio" name="tjzq" title="" lay-skin="primary" checked>
          <div @click="radio==2?(radio=1,getConverter()):''" :class="{'layui-form-radioed':radio==1}" class="layui-unselect layui-form-radio">
            <i :class="{'layui-anim-scaleSpring':radio==1}" class="layui-anim layui-icon"></i>
            <div></div>
          </div>
          <input type="radio" name="tjzq" title="" lay-skin="primary" checked>
          <div @click="radio==1?(radio=2,getConverter()):''"  :class="{'layui-form-radioed':radio==2}" class="layui-unselect layui-form-radio">
            <i :class="{'layui-anim-scaleSpring':radio==2}" class="layui-anim layui-icon"></i>
            <div></div>
          </div>
          <div class="layui-input-inline dlcxrqk">
            <el-date-picker
            v-show="radio==1"
            @change="getConverter"
              v-model="dateSD"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
            <el-date-picker
            v-show="radio==2"
            @change="getConverter"
                type="month"
                v-model="date"
                value-format="yyyy-MM"
                placeholder="">
                </el-date-picker>
          </div>
        </div>
        <div class="cxsjdata layui-tab layui-tab-brief">
          <ul class="layui-tab-title xspytop">
            <li :class="{'layui-this':tbChange}" @click="tbChange=true">
              <i class="yun_icon_sjtu"></i>
            </li>
            <li :class="{'layui-this':!tbChange}" @click="tbChange=false">
              <i class="yun_icon_sjbiao"></i>
            </li>
          </ul>
          <div class="layui-tab-content">
            <div :class="['layui-tab-item',{'layui-show':tbChange}]" id="dlcx_sjstbox" style="height: 500px;">
              <!---->
            </div>
            <div :class="['layui-tab-item',{'layui-show':!tbChange}]" style="height: 500px;">
              <h3 style="font-size:18px;font-weight:normal;text-align:center;margin-top:10px;"> 2#</h3>
              <el-table :data="tableData">
                    <el-table-column type="index" label=""></el-table-column>
                    <el-table-column prop="name" label=""></el-table-column>
                    <el-table-column prop="date" label=""></el-table-column>
                    <el-table-column prop="value1" label=""></el-table-column>
                    <el-table-column prop="value2" label=""></el-table-column>
                    <el-table-column prop="value3" label=""></el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--END-->
    <div style="clear:both;"></div>
  </div>
</template>
<script>
import checkBox from "./checkBox";
export default {
  data: function() {
    return {
      date:'',
      dateSD:'',
      showMore: false,
      filter1: [],
      filter2: [],
      filter3: [],
      filter4: [],
      filterNames: ["", "", "", ""],
      tbChange:true,//
      radio:1,
      tableData:[]
    };
  },
  components: { checkBox },
  mounted(){
        this.getDefaultTime();
        this.getEchart();
        for(var i=0;i<10;i++){
          this.tableData.push({
            name:'',
            date:'2018-08-08',
            value1:4556,
            value2:4556,
            value3:4556
          })
        }
  },
  methods: {
    //
    getDefaultTime(){
      this.date=this.$getDate('month',new Date())
        var nowDate=new Date();
        var a=new Date(nowDate.toLocaleDateString())
        var sDate=new Date(a-30*24*3600000)
        this.dateSD=[sDate,a];
    },
    //
    changeCheck(num, a) {
      this.getConverter();
      if (num == 1) {
        this.filter1 = a;
      } else if (num == 2) {
        this.filter2 = a;
      } else if (num == 3) {
        this.filter3 = a;
      } else if (num == 4) {
        this.filter4 = a;
      }
    },
    //
    delCheck(index, indexd) {
      if (index == 0) {
        this.$refs.check1.delCheck(indexd);
      } else if (index == 1) {
        // this.$refs.check2.delCheck(indexd);
      } else if (index == 2) {
        this.$refs.check3.delCheck(indexd);
      } else if (index == 3) {
        this.$refs.check4.delCheck(indexd);
      }
    },
    //
    delCheckAll() {
      this.$refs.check1.delCheckAll();
      // this.$refs.check2.delCheckAll();
      this.$refs.check3.delCheckAll();
      this.$refs.check4.delCheckAll();
    },
    //
    getEchart() {
      var dlcx_sjst = echarts.init(
        document.getElementById("dlcx_sjstbox"),
        "yundw"
      );
      var dlcx_sjst_option = {
        title: {
          text: " 2#",
          subtext: "",
          x: "center",
          y: 5,
          textStyle: {
            color: "#333",
            fontSize: "18",
            fontWeight: "normal"
          }
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "70",
          left: "2%",
          right: "4%",
          bottom: "2%",
          containLabel: true
        },
        legend: {
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          data: ["", "", ""],
          right: "center",
          y: "35",
          textStyle: {
            fontSize: 12,
            color: "#555"
          }
        },
        xAxis: [
          {
            name: "",
            boundaryGap: false,
            data: [
              "2018-10-18",
              "2018-10-19",
              "2018-10-20",
              "2018-10-21",
              "2018-10-22",
              "2018-10-23",
              "2018-10-24",
              "2018-10-25",
              "2018-10-26",
              "2018-10-27",
              "2018-10-28",
              "2018-10-29"
            ]
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "Kw/h",
            axisLine: {
              lineStyle: {
                color: "#ccc"
              }
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 14
              }
            },
            splitLine: {
              lineStyle: {
                color: "#ccc"
              }
            }
          }
        ],
        series: [
          {
            name: "",

            type: "line",
            // smooth: true,
            symbol: "circle",
            symbolSize: 5,
            // showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
              }
            },

            data: [220, 182, 191, 134, 150, 120, 110, 125, 145, 122, 165, 122]
          },
          {
            name: "",
            type: "line",
            // smooth: true,
            symbol: "circle",
            symbolSize: 5,
            // showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
              }
            },

            data: [120, 110, 125, 145, 122, 165, 122, 220, 182, 191, 134, 150]
          },
          {
            name: "",
            type: "line",
            // smooth: true,
            symbol: "circle",
            symbolSize: 5,
            // showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
              }
            },

            data: [220, 182, 125, 145, 122, 191, 134, 150, 120, 110, 165, 122]
          }
        ]
      };
      dlcx_sjst.setOption(dlcx_sjst_option);
    },
    //
    getConverter(){
      var obj={dataType:this.radio}
     if(this.radio==1){
       obj.startTime=this.$getDate('day',this.dateSD[0])
       obj.endTime=this.$getDate('day',this.dateSD[1])
     }else{
       obj.month=this.date;
     }
      this.$http
      .get("http://192.168.43.61:22890/hnsgService/hnsgQuery/getConverter?",{
        params:obj
      })

    }
  },
  watch:{
    
  }
};
</script>
<style>
.dlcx .dlcxrqk input{
      height:30px;
}
.dlcx .dlcxrqk .el-date-editor--daterange input{
      height:29px;
}
.dlcx .dlcs_search{
  text-align:left;
}
.dlcx .sctjdatabox{
  text-align:left;
}
</style>



